import { defineStore } from 'pinia';

export const useCartStore = defineStore('cart', {
    state: () => ({
        cart: [],
        totalPrice: 0
    }),
    actions: {
        removeFromCart(product) {
            const idx = this.cart.findIndex((p) => p.key === product.key);
            this.cart.splice(idx, 1);
        },
        addToCart(product) {
            const foundProduct = this.cart.find((p) => p.key === product.key);
            if (foundProduct) {
                foundProduct.cnt++;
                return;
            }
            const exists = this.cart.some((p) => p.key === product.key);
            if (exists) {
                return;
            }
            this.cart.push(product);
        },
        increase(product) {
            const idx = this.cart.findIndex((p) => p.key === product.key);
            this.cart[idx].cnt++;
        },

        decrease(product) {
            const idx = this.cart.findIndex((p) => p.key === product.key);
            if (this.cart[idx].cnt === 1) {
                this.cart.splice(idx, 1);
                return;
            }
            this.cart[idx].cnt--;
        }

    }

});