<template>
  <header>
    <div class="buttons-container">
      <button class="button" @click="showLogin">ورود به حساب کاربری</button>
    </div>
    <div class="custom-select">
      <select>
        <option>راهنما و خدملت</option>
        <option>محصولات جدید</option>
        <option>فهرست تولید کنندگان</option>
        <option>درباره ما</option>
        <option>پرسش های متداول</option>
      </select>
      <p>اخبار</p>
      <p>بلاگ</p>
      <p>دریافت لینک اپلیکیشن</p>
      <p>درخواست خرید اقساطی</p>
    </div>
    <input
      type="text"
      v-model="searchInput"
      placeholder="جستجو در فروشگاه"
      dir="rtl"
      style="text-align: right"
      @input="search"
    />
    <div class="shopping-cart">
      <i class="material-icons" @click="goToCart">shopping_cart</i>
    </div>
    <div class="telephone">تماس با ما 242635989-021 و 742824989-021</div>
    <select class="select-city">
      <option v-for="city in cities" :key="city.id" class="option">
        {{ city.name }}
      </option>
    </select>
    <div class="menu-container">
      <select>
        <option>نان</option>
      </select>
      <select>
        <option>اقلام غیر خوراکی</option>
        <option>لوازم تحریر</option>
        <option>آرایشی و بهداشتی</option>
        <option>شوینده</option>
      </select>
      <select>
        <option>اقلام خوراکی</option>
        <option>نوشیدنی</option>
        <option>لبنیات</option>
        <option>تنقلات</option>
        <option>خواربار</option>
        <option>کنسرو و غذای آماده</option>
        <option>چای و ادویه</option>
        <option>چای و دمنوش</option>
        <option>شکلات و شیرینی</option>
      </select>

      <p>تخفیفات ویژه</p>
      <select>
        <option>محصولات تنظیم بازار</option>
      </select>

      <select>
        <option>میوه و سبزیجات</option>
      </select>
      <p>کل محصولات</p>
    </div>
    <div class="search-results-container">
      <div
        v-for="product in searchResults"
        :key="product.key"
        class="result-box"
      >
        <p>{{ product.name }}</p>
        <p>{{ product.price }}</p>
        <img :src="product.image" alt="product" />
      </div>
    </div>
  </header>
</template>

<script>
//used store
import { useDataStore } from "../../store/db.js";

export default {
  data() {
    return {
      cities: [],
      allProducts: [],
      searchInput: "",
      searchResults: [],
      fruits: [],
      tanzim: [],
      takhfif: [],
      khoraki: [],
      gheireKhoraki: [],
      nan: [],
    };
  },
  mounted() {
    const store = useDataStore();

    this.fruits = store.fruits;
    this.tanzim = store.tanzim;
    this.takhfif = store.takhfif;
    this.khoraki = store.khoraki;
    this.gheireKhoraki = store.gheireKhoraki;
    this.nan = store.nan;
    this.allProducts = [
      ...this.fruits,
      ...this.tanzim,
      ...this.takhfif,
      ...this.khoraki,
      ...this.gheireKhoraki,
      ...this.nan,
    ];
    this.cities = store.cities;
  },
  methods: {
    goToCart() {
      this.$router.push({ name: "cart" });
    },
    showLogin() {
      this.$router.push({ name: "login" });
    },
    search() {
      if (this.allProducts) {
        if (this.searchInput === "") {
          this.searchResults = "";
        } else {
          const searchQuery = this.searchInput;
          this.searchResults = this.allProducts.filter((product) => {
            return product.name.includes(searchQuery);
          });
        }
      }
    },
  },
};
</script>

