<template>
  <Header />
  <div class="product-container">
    <div class="product-box" v-for="product in products" :key="product.key">
      <img :src="product.image" :alt="product.name" class="product-image" />
      <router-link
        :to="{
          name: 'productDetails',
          params: { key: product.key, type: product.type },
        }"
      >
        <p class="product-name">{{ product.name }}</p>
      </router-link>
      <p class="product-weight">{{ product.weight }}</p>
      <p class="product-price">{{ product.price }}</p>
      <i
        class="material-icons product-delete-icon"
        @click="deleteProduct(product)"
        >delete</i
      >
      <div class="change-count">
        <div class="increase">
          <i class="material-icons increase" @click="increase(product)"
            >add</i
          >
        </div>
        <div class="count">{{ product.cnt }}</div>
        <div class="decrease">
          <i class="material-icons increase" @click="decrease(product)">remove</i>
        </div>
      </div>
    </div>
    <div class="cart-payment">
      <div class="total-price">
        <div class="my-text-total-price">قیمت کل:ریال{{ totalPrice }}</div>
      </div>
      <button class="payment" @click="goToPayment">پرداخت</button>
    </div>
  </div>
  
  <Footer />
</template>

<script>
import { useCartStore } from '../../store/cart.js';
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";

export default {
  components: { Header, Footer },
  data() {
    return {
      products: [],
      totalPrice: 0
    };
  },
  mounted() {
    const cart = useCartStore();
    this.products = cart.cart;
    this.calculateTotalPrice();
  },
  methods: {
    deleteProduct(product) {
      const cart = useCartStore();
      cart.removeFromCart(product);
    },
    increase(product) {
      const cart = useCartStore();
      cart.increase(product);
    },
    decrease(product) {
      const cart = useCartStore();
      cart.decrease(product);
    },
    calculateTotalPrice(){
      this.totalPrice = 0;
      this.products.forEach(product => {
        this.totalPrice += parseInt(product.price.match(/\d+/)[0])*product.cnt;
      })
      const cart = useCartStore();
      cart.totalPrice = this.totalPrice;
    },
    goToPayment() {
      window.location.href = 'https://idpay.ir/myshopinpishgaman';
    },
  },
};
</script>

