export default {
  data() {
    const store = useDataStore();
    return {
      showCities: true,
      cities: store.cities,
      fruits: store.fruits,
      tanzim: store.tanzim,
      takhfif: store.takhfif,
      khoraki: store.khoraki,
      gheireKhoraki: store.gheireKhoraki,
      nan: store.nan,
      filtered: store.filtered,
    };
  },
  methods: {
    changeModal() {
      this.showCities = !this.showCities;
    },
    addToCart(product) {
      let p = {
        name: product.name,
        price: product.price,
        weight: product.weight,
        image: product.image,
        id: product.id,
        type: product.type,
        key: product.key,
        cnt: 1,
      };
      const cart = useCartStore();
      cart.addToCart(p);
      this.$router.push({ name: "cart" });
    },
  },
  components: { Header, Footer },
};
