<template>
  <Header />
  <div class="filtered-container">
    <div class="product-box" v-for="product in results" :key="product.key">
      <img :src="product.image" :alt="product.name" class="product-image" />
      <router-link
        :to="{
          name: 'productDetails',
          params: { key: product.key, type: product.type },
        }"
      >
        <p class="product-name">{{ product.name }}</p>
      </router-link>
      <p class="product-weight">{{ product.weight }}</p>
      <p class="product-price">{{ product.price }}</p>
      <button class="product-button" @click="addToCart(product)">
        افزودن به سبد خرید
      </button>
    </div>
  </div>
  <Footer />
</template>

<script>
import Footer from "@/components/Footer.vue";
import Header from "@/components/Header.vue";

export default {
  components: { Header, Footer },
  data() {
    return {
      allProducts: [],
      searchInput: "",
      searchResults: [],
      fruits: [],
      tanzim: [],
      takhfif: [],
      khoraki: [],
      gheireKhoraki: [],
      nan: [],
      results: [],
    };
  },
  methods: {
    addToCart(product) {
      let p = {
        name: product.name,
        price: product.price,
        weight: product.weight,
        image: product.image,
        id: product.id,
        type: product.type,
        key: product.key,
        cnt: 1,
      };
      fetch("http://localhost:3000/cart", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(p),
      });
      this.$router.push({ name: "cart" });
    },
  },
  mounted() {
    const fetchFruits = fetch("http://localhost:3000/fruits");
    const fetchTanzim = fetch("http://localhost:3000/tanzim");
    const fetchTakhfif = fetch("http://localhost:3000/takhfif");
    const fetchKhoraki = fetch("http://localhost:3000/khoraki");
    const fetchGheireKhoraki = fetch("http://localhost:3000/gheireKhoraki");
    const fetchNan = fetch("http://localhost:3000/nan");

    Promise.all([
      fetchFruits,
      fetchTanzim,
      fetchTakhfif,
      fetchKhoraki,
      fetchGheireKhoraki,
      fetchNan,
    ])
      .then((responses) => Promise.all(responses.map((res) => res.json())))
      .then((data) => {
        this.fruits = data[0];
        this.tanzim = data[1];
        this.takhfif = data[2];
        this.khoraki = data[3];
        this.gheireKhoraki = data[4];
        this.nan = data[5];
        this.allProducts = [
          ...this.fruits,
          ...this.tanzim,
          ...this.takhfif,
          ...this.khoraki,
          ...this.gheireKhoraki,
          ...this.nan,
        ];
      })
      .catch((err) => console.log(err.message));
    for (let i = 0; i < this.allProducts.length; i++) {
      if (this.allProducts[i].category === this.$route.params.category) {
        this.results.push(this.allProducts[i]);
      }
    }
    console.log(this.allProducts[0].category);
    console.log(this.allProducts[0].category);
  },
};
</script>

