        </div>
      </div>
    </div>
    <img :src="product.detail_image" alt="image" />
  </div>
  <Footer />
</template>

<script>
import Footer from "@/components/Footer.vue";
import Header from "@/components/Header.vue";

export default {
  components: { Header, Footer },
  data() {
    return {
      product: null,
    };
  },
  mounted() {
    fetch("http://localhost:3000/" + this.$route.params.type + "/" + this.$route.params.key)
